import React, { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { Menu, X, Sun, Moon, Globe, ChevronDown } from "lucide-react";
import Logo from "./Logo.tsx";
import { useLanguage } from "../contexts/LanguageContext.tsx";
import { useTheme } from "../contexts/ThemeContext.tsx";

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isLanguageOpen, setIsLanguageOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const { language, setLanguage, t } = useLanguage();
  const { theme, toggleTheme } = useTheme();

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 50);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const navItems = [
    { href: "#home", key: "nav.home" },
    { href: "#about", key: "nav.about" },
    { href: "#skills", key: "nav.skills" },
    { href: "#portfolio", key: "nav.portfolio" },
    { href: "#contact", key: "nav.contact" },
  ];

  const languages = [
    { code: 'en', name: 'English', flag: '' },
    { code: 'az', name: 'Azrbaycan', flag: '' },
    { code: 'tr', name: 'Trke', flag: '' },
  ];

  const scrollToSection = (href: string) => {
    const element = document.querySelector(href);
    if (element) {
      element.scrollIntoView({ behavior: "smooth" });
    }
    setIsMenuOpen(false);
  };

  const scrollToTop = () => {
    const homeElement = document.querySelector('#home');
    if (homeElement) {
      homeElement.scrollIntoView({ behavior: "smooth" });
    } else {
      window.scrollTo({
        top: 0,
        behavior: "smooth"
      });
    }
    setIsMenuOpen(false);
  };

  const isDark = theme === 'dark';

  return (
    <motion.header
      initial={{ y: -100 }}
      animate={{ y: 0 }}
      className={`fixed top-0 left-0 right-0 z-50 transition-all duration-300 ${
        scrolled
          ? isDark 
            ? "bg-slate-900/90 backdrop-blur-md shadow-2xl border-b border-white/10"
            : "bg-white/90 backdrop-blur-md shadow-2xl border-b border-gray-200"
          : "bg-transparent"
      }`}
    >
      <div className="container mx-auto px-2 sm:px-4 py-2 sm:py-3">
        <div className="flex items-center justify-between">
          {/* Logo - Fixed spacing and mobile layout */}
          <motion.button
            onClick={(e) => {
              e.preventDefault();
              e.stopPropagation();
              scrollToTop();
            }}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className="flex items-center space-x-1 sm:space-x-2 cursor-pointer z-10 relative"
            type="button"
          >
            <Logo size={24} className="sm:w-8 sm:h-8" />
            <div className="flex flex-col min-w-0">
              <span className={`text-sm sm:text-base lg:text-lg font-bold bg-gradient-to-r from-blue-500 via-purple-500 to-pink-500 bg-clip-text text-transparent ${isDark ? 'text-white' : 'text-gray-900'} truncate`}>
                BGDev
              </span>
              <span className={`text-xs ${isDark ? 'text-gray-400' : 'text-gray-500'} truncate hidden sm:block`}>
                Welcome to my portfolio
              </span>
            </div>
          </motion.button>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex items-center space-x-6 lg:space-x-8">
            {navItems.map((item, index) => (
              <motion.a
                key={item.href}
                href={item.href}
                onClick={(e) => {
                  e.preventDefault();
                  scrollToSection(item.href);
                }}
                whileHover={{ y: -2 }}
                className={`text-sm lg:text-base font-medium transition-colors duration-300 ${
                  isDark ? 'text-gray-300 hover:text-white' : 'text-gray-600 hover:text-gray-900'
                }`}
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.5, delay: index * 0.1 }}
              >
                {t(item.key)}
              </motion.a>
            ))}
          </nav>

          {/* Desktop Controls */}
          <div className="hidden md:flex items-center space-x-4">
            {/* Theme Toggle */}
            <motion.button
              onClick={toggleTheme}
              whileHover={{ scale: 1.1 }}
              whileTap={{ scale: 0.9 }}
              className={`p-2 rounded-lg transition-all duration-300 ${
                isDark 
                  ? 'bg-slate-800 hover:bg-slate-700 text-yellow-400' 
                  : 'bg-gray-100 hover:bg-gray-200 text-gray-600'
              }`}
            >
              {isDark ? <Sun size={20} /> : <Moon size={20} />}
            </motion.button>

            {/* Language Selector */}
            <div className="relative">
              <motion.button
                onClick={() => setIsLanguageOpen(!isLanguageOpen)}
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                className={`flex items-center space-x-2 px-3 py-2 rounded-lg transition-all duration-300 ${
                  isDark 
                    ? 'bg-slate-800 hover:bg-slate-700 text-gray-300' 
                    : 'bg-gray-100 hover:bg-gray-200 text-gray-600'
                }`}
              >
                <Globe size={18} />
                <span className="text-sm font-medium">
                  {languages.find(lang => lang.code === language)?.flag}
                </span>
                <ChevronDown size={16} className={`transition-transform duration-300 ${isLanguageOpen ? 'rotate-180' : ''}`} />
              </motion.button>

              <AnimatePresence>
                {isLanguageOpen && (
                  <motion.div
                    initial={{ opacity: 0, y: -10, scale: 0.95 }}
                    animate={{ opacity: 1, y: 0, scale: 1 }}
                    exit={{ opacity: 0, y: -10, scale: 0.95 }}
                    className={`absolute top-full right-0 mt-2 py-2 rounded-lg shadow-xl border z-50 min-w-[120px] ${
                      isDark 
                        ? 'bg-slate-800 border-white/10' 
                        : 'bg-white border-gray-200'
                    }`}
                  >
                    {languages.map((lang) => (
                      <button
                        key={lang.code}
                        onClick={() => {
                          setLanguage(lang.code);
                          setIsLanguageOpen(false);
                        }}
                        className={`flex items-center space-x-3 px-4 py-2 text-sm w-full text-left transition-colors duration-300 hover:bg-opacity-80 ${
                          language === lang.code
                            ? isDark 
                              ? 'bg-blue-600 text-white' 
                              : 'bg-blue-100 text-blue-600'
                            : isDark 
                              ? 'text-gray-300 hover:bg-slate-700' 
                              : 'text-gray-700 hover:bg-gray-100'
                        }`}
                      >
                        <span className="text-lg">{lang.flag}</span>
                        <span className="font-medium">{lang.name}</span>
                      </button>
                    ))}
                  </motion.div>
                )}
              </AnimatePresence>
            </div>
          </div>

          {/* Mobile Menu Button */}
          <motion.button
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className={`md:hidden p-2 rounded-lg transition-all duration-300 ${
              isDark 
                ? 'bg-slate-800 hover:bg-slate-700 text-gray-300' 
                : 'bg-gray-100 hover:bg-gray-200 text-gray-600'
            }`}
          >
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </motion.button>
        </div>
      </div>

      {/* Mobile Menu */}
      <AnimatePresence>
        {isMenuOpen && (
          <motion.div
            initial={{ opacity: 0, height: 0 }}
            animate={{ opacity: 1, height: 'auto' }}
            exit={{ opacity: 0, height: 0 }}
            className={`md:hidden overflow-hidden ${
              isDark 
                ? 'bg-slate-900/95 backdrop-blur-md border-t border-white/10' 
                : 'bg-white/95 backdrop-blur-md border-t border-gray-200'
            }`}
          >
            <div className="px-4 py-6 space-y-6">
              {/* Mobile Navigation */}
              <nav className="space-y-4">
                {navItems.map((item, index) => (
                  <motion.a
                    key={item.href}
                    href={item.href}
                    onClick={(e) => {
                      e.preventDefault();
                      scrollToSection(item.href);
                    }}
                    className={`block text-lg font-medium transition-colors duration-300 ${
                      isDark ? 'text-gray-300 hover:text-white' : 'text-gray-600 hover:text-gray-900'
                    }`}
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ duration: 0.3, delay: index * 0.1 }}
                  >
                    {t(item.key)}
                  </motion.a>
                ))}
              </nav>

              {/* Divider */}
              <div className={`h-px ${isDark ? 'bg-white/10' : 'bg-gray-200'}`} />

              {/* Mobile Controls */}
              <div className="flex items-center justify-between">
                {/* Theme Toggle */}
                <motion.button
                  onClick={toggleTheme}
                  whileHover={{ scale: 1.05 }}
                  whileTap={{ scale: 0.95 }}
                  className={`p-3 rounded-lg transition-all duration-300 ${
                    isDark 
                      ? 'bg-slate-800 hover:bg-slate-700 text-yellow-400' 
                      : 'bg-gray-100 hover:bg-gray-200 text-gray-600'
                  }`}
                >
                  {isDark ? <Sun size={20} /> : <Moon size={20} />}
                </motion.button>

                {/* Language Selector */}
                <div className="relative">
                  <motion.button
                    onClick={() => setIsLanguageOpen(!isLanguageOpen)}
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    className={`flex items-center space-x-2 px-4 py-3 rounded-lg transition-all duration-300 ${
                      isDark 
                        ? 'bg-slate-800 hover:bg-slate-700 text-gray-300' 
                        : 'bg-gray-100 hover:bg-gray-200 text-gray-600'
                    }`}
                  >
                    <Globe size={20} />
                    <span className="text-base font-medium">
                      {languages.find(lang => lang.code === language)?.flag}
                    </span>
                    <ChevronDown size={18} className={`transition-transform duration-300 ${isLanguageOpen ? 'rotate-180' : ''}`} />
                  </motion.button>

                  <AnimatePresence>
                    {isLanguageOpen && (
                      <motion.div
                        initial={{ opacity: 0, y: -10, scale: 0.95 }}
                        animate={{ opacity: 1, y: 0, scale: 1 }}
                        exit={{ opacity: 0, y: -10, scale: 0.95 }}
                        className={`absolute top-full right-0 mt-2 py-2 rounded-lg shadow-xl border min-w-[200px] z-50 ${
                          isDark 
                            ? 'bg-slate-800 border-white/10' 
                            : 'bg-white border-gray-200'
                        }`}
                      >
                        {languages.map((lang) => (
                          <button
                            key={lang.code}
                            onClick={() => {
                              setLanguage(lang.code);
                              setIsLanguageOpen(false);
                            }}
                            className={`flex items-center space-x-3 px-4 py-3 text-base w-full text-left transition-colors duration-300 hover:bg-opacity-80 ${
                              language === lang.code
                                ? isDark 
                                  ? 'bg-blue-600 text-white' 
                                  : 'bg-blue-100 text-blue-600'
                                : isDark 
                                  ? 'text-gray-300 hover:bg-slate-700' 
                                  : 'text-gray-700 hover:bg-gray-100'
                            }`}
                          >
                            <span className="text-xl">{lang.flag}</span>
                            <span className="font-medium">{lang.name}</span>
                          </button>
                        ))}
                      </motion.div>
                    )}
                  </AnimatePresence>
                </div>
              </div>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </motion.header>
  );
};

export default Header;