import React, { useEffect, useState } from "react";
import { motion, useAnimation } from "framer-motion";
import { Download, Mail, Github, Linkedin, ArrowRight, Sparkles } from "lucide-react";
import { useLanguage } from "../contexts/LanguageContext.tsx";
import { useTheme } from "../contexts/ThemeContext.tsx";

const Hero = () => {
  const { t } = useLanguage();
  const { theme } = useTheme();
  const isDark = theme === 'dark';
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const controls = useAnimation();

  useEffect(() => {
    const handleMouseMove = (e: MouseEvent) => {
      setMousePosition({ x: e.clientX, y: e.clientY });
    };

    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  useEffect(() => {
    controls.start({
      background: [
        "linear-gradient(45deg, #667eea 0%, #764ba2 100%)",
        "linear-gradient(45deg, #f093fb 0%, #f5576c 100%)",
        "linear-gradient(45deg, #4facfe 0%, #00f2fe 100%)",
        "linear-gradient(45deg, #43e97b 0%, #38f9d7 100%)",
        "linear-gradient(45deg, #667eea 0%, #764ba2 100%)"
      ],
      transition: {
        duration: 8,
        repeat: Infinity,
        ease: "linear"
      }
    });
  }, [controls]);

  const particles = Array.from({ length: 20 }, (_, i) => i);

  return (
    <section id="home" className="relative min-h-screen flex items-center justify-center overflow-hidden px-2 sm:px-4">
      {/* Dynamic Gradient Background */}
      <motion.div
        animate={controls}
        className="absolute inset-0 opacity-20"
      />
      
      {/* Animated Background Pattern */}
      <div className="absolute inset-0">
        <div className="absolute inset-0 bg-gradient-to-r from-blue-500/10 via-purple-500/10 to-pink-500/10" />
        <div className="absolute inset-0 bg-[radial-gradient(circle_at_50%_50%,rgba(120,119,198,0.3),transparent_50%)]" />
      </div>

      {/* Mouse Tracking Particles */}
      {particles.map((_, index) => (
        <motion.div
          key={index}
          className="absolute w-1 h-1 bg-blue-400 rounded-full pointer-events-none"
          animate={{
            x: mousePosition.x + Math.sin(index * 0.5) * 50,
            y: mousePosition.y + Math.cos(index * 0.5) * 50,
            opacity: [0, 1, 0],
            scale: [0, 1, 0],
          }}
          transition={{
            duration: 2,
            repeat: Infinity,
            delay: index * 0.1,
            ease: "easeOut"
          }}
        />
      ))}

      {/* Floating Elements */}
      <motion.div
        animate={{
          y: [0, -20, 0],
          rotate: [0, 5, 0],
        }}
        transition={{
          duration: 6,
          repeat: Infinity,
          ease: "easeInOut"
        }}
        className="absolute top-10 sm:top-20 left-10 sm:left-20 w-16 h-16 sm:w-32 sm:h-32 bg-gradient-to-r from-blue-400 to-purple-400 rounded-full opacity-20 blur-xl"
      />
      
      <motion.div
        animate={{
          y: [0, 20, 0],
          rotate: [0, -5, 0],
        }}
        transition={{
          duration: 8,
          repeat: Infinity,
          ease: "easeInOut",
          delay: 2
        }}
        className="absolute bottom-10 sm:bottom-20 right-10 sm:right-20 w-20 h-20 sm:w-40 sm:h-40 bg-gradient-to-r from-pink-400 to-red-400 rounded-full opacity-20 blur-xl"
      />

      <div className="relative z-10 text-center max-w-4xl mx-auto">
        {/* Welcome Badge */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          className="inline-flex items-center space-x-2 px-3 py-2 sm:px-6 sm:py-3 rounded-full bg-gradient-to-r from-blue-500/20 to-purple-500/20 border border-blue-500/30 mb-4 sm:mb-6"
        >
          <Sparkles size={16} className="text-blue-400 sm:w-5 sm:h-5" />
          <span className={`text-xs sm:text-sm font-medium ${isDark ? 'text-blue-300' : 'text-blue-600'}`}>
            {t('hero.welcome')}
          </span>
        </motion.div>

        {/* Main Title */}
        <motion.h1
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.2 }}
          className={`text-3xl sm:text-4xl md:text-6xl font-bold mb-4 sm:mb-6 ${isDark ? 'text-white' : 'text-gray-900'}`}
        >
          Babak Gasimzade
        </motion.h1>

        {/* Subtitle */}
        <motion.p
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.4 }}
          className={`text-sm sm:text-base md:text-xl mb-6 sm:mb-8 max-w-2xl mx-auto leading-relaxed ${isDark ? 'text-gray-300' : 'text-gray-600'}`}
        >
          {t('hero.subtitle')}
        </motion.p>

        {/* Description */}
        <motion.p
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.6 }}
          className={`text-xs sm:text-sm md:text-base mb-8 sm:mb-12 max-w-3xl mx-auto leading-relaxed ${isDark ? 'text-gray-400' : 'text-gray-500'}`}
        >
          {t('hero.description')}
        </motion.p>

        {/* Action Buttons */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.8 }}
          className="flex flex-col sm:flex-row gap-3 sm:gap-4 justify-center items-center mb-8 sm:mb-12"
        >
          {/* Download CV Button */}
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className="group flex items-center space-x-2 px-4 py-2 sm:px-6 sm:py-3 bg-gradient-to-r from-blue-500 to-purple-600 text-white rounded-lg font-medium text-sm sm:text-base transition-all duration-300 shadow-lg hover:shadow-xl"
            onClick={() => {
              const link = document.createElement('a');
              link.href = '/CV - Babak Gasimzade Latvia eu.pdf';
              link.download = 'Babak_Gasimzade_CV.pdf';
              link.click();
            }}
          >
            <Download size={16} className="sm:w-5 sm:h-5" />
            <span>{t('hero.downloadCV')}</span>
            <ArrowRight size={16} className="group-hover:translate-x-1 transition-transform sm:w-5 sm:h-5" />
          </motion.button>

          {/* Contact Button */}
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            className="group flex items-center space-x-2 px-4 py-2 sm:px-6 sm:py-3 border-2 border-blue-500 text-blue-500 hover:bg-blue-500 hover:text-white rounded-lg font-medium text-sm sm:text-base transition-all duration-300"
            onClick={() => document.getElementById('contact')?.scrollIntoView({ behavior: 'smooth' })}
          >
            <Mail size={16} className="sm:w-5 sm:h-5" />
            <span>{t('hero.contact')}</span>
            <ArrowRight size={16} className="group-hover:translate-x-1 transition-transform sm:w-5 sm:h-5" />
          </motion.button>
        </motion.div>

        {/* Social Links */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 1.0 }}
          className="flex justify-center space-x-4 sm:space-x-6 mb-8 sm:mb-12"
        >
          {/* LinkedIn */}
          <motion.a
            href="https://www.linkedin.com/in/babak-gasimzade"
            target="_blank"
            rel="noopener noreferrer"
            whileHover={{ scale: 1.1, y: -2 }}
            whileTap={{ scale: 0.9 }}
            className="p-2 sm:p-3 bg-blue-600 text-white rounded-full transition-all duration-300 hover:bg-blue-700 shadow-lg hover:shadow-xl"
          >
            <Linkedin size={20} className="sm:w-6 sm:h-6" />
          </motion.a>

          {/* Email */}
          <motion.a
            href="mailto:babak.gasimzade@gmail.com"
            whileHover={{ scale: 1.1, y: -2 }}
            whileTap={{ scale: 0.9 }}
            className="p-2 sm:p-3 bg-red-500 text-white rounded-full transition-all duration-300 hover:bg-red-600 shadow-lg hover:shadow-xl"
          >
            <Mail size={20} className="sm:w-6 sm:h-6" />
          </motion.a>

          {/* GitHub */}
          <motion.a
            href="https://github.com/bgasimzade99"
            target="_blank"
            rel="noopener noreferrer"
            whileHover={{ scale: 1.1, y: -2 }}
            whileTap={{ scale: 0.9 }}
            className={`p-2 sm:p-3 rounded-full transition-all duration-300 shadow-lg hover:shadow-xl ${isDark ? 'bg-gray-800 text-white hover:bg-gray-700' : 'bg-gray-900 text-white hover:bg-gray-800'}`}
          >
            <Github size={20} className="sm:w-6 sm:h-6" />
          </motion.a>
        </motion.div>

        {/* Role Badge */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 1.2 }}
          className="inline-flex items-center space-x-2 px-3 py-2 sm:px-6 sm:py-3 rounded-full bg-gradient-to-r from-purple-500/20 to-pink-500/20 border border-purple-500/30"
        >
          <Sparkles size={16} className="text-purple-400 sm:w-5 sm:h-5" />
          <span className={`text-xs sm:text-sm font-medium ${isDark ? 'text-purple-300' : 'text-purple-600'}`}>
            {t('hero.role')}
          </span>
        </motion.div>
      </div>
    </section>
  );
};

export default Hero; 