import React, { useEffect, useState } from "react";
import { motion, useAnimation, useInView } from "framer-motion";
import { Code, Smartphone, Palette, Sparkles, Zap, TrendingUp } from "lucide-react";
import { useLanguage } from "../contexts/LanguageContext.tsx";
import { useTheme } from "../contexts/ThemeContext.tsx";

const Skills = () => {
  const { t } = useLanguage();
  const { theme } = useTheme();
  const isDark = theme === 'dark';
  const [hoveredSkill, setHoveredSkill] = useState(null);
  
  // Skill categories with proper structure
  const skillCategories = [
    {
      title: t('skills.frontend'),
      icon: Code,
      skills: [
        { name: "React.js", level: 85 },
        { name: "JavaScript", level: 80 },
        { name: "HTML/CSS", level: 90 },
        { name: "TypeScript", level: 70 },
        { name: "Tailwind CSS", level: 85 }
      ]
    },
    {
      title: t('skills.mobile'),
      icon: Smartphone,
      skills: [
        { name: "React Native", level: 75 },
        { name: "Expo", level: 80 },
        { name: "Mobile UI/UX", level: 75 },
        { name: "App Store Deployment", level: 70 }
      ]
    },
    {
      title: t('skills.tools'),
      icon: Palette,
      skills: [
        { name: "Git/GitHub", level: 80 },
        { name: "Figma", level: 75 },
        { name: "VS Code", level: 85 },
        { name: "npm/yarn", level: 85 }
      ]
    }
  ];

  // Technologies array
  const technologies = [
    { name: t('skills.tech.react'), icon: "", level: t('skills.level.good') },
    { name: t('skills.tech.reactNative'), icon: "", level: t('skills.level.good') },
    { name: t('skills.tech.javascript'), icon: "", level: t('skills.level.good') },
    { name: t('skills.tech.htmlcss'), icon: "", level: t('skills.level.veryGood') },
    { name: t('skills.tech.expo'), icon: "", level: t('skills.level.good') },
    { name: t('skills.tech.typescript'), icon: "", level: t('skills.level.medium') },
    { name: t('skills.tech.figma'), icon: "", level: t('skills.level.good') },
    { name: t('skills.tech.tailwind'), icon: "", level: t('skills.level.veryGood') }
  ];

  // Learning items
  const learningItems = [
    { name: t('skills.learning.flutter'), icon: "" },
    { name: t('skills.learning.swift'), icon: "" },
    { name: t('skills.learning.kotlin'), icon: "" },
    { name: t('skills.learning.nextjs'), icon: "" },
    { name: t('skills.learning.testing'), icon: "" }
  ];

  const controls = useAnimation();
  const ref = React.useRef(null);
  const isInView = useInView(ref, { once: false });

  useEffect(() => {
    if (isInView) {
      controls.start("visible");
    }
  }, [isInView, controls, t]);

  return (
    <section id="skills" className={`py-12 sm:py-16 md:py-20 relative overflow-hidden ${isDark ? 'bg-slate-900' : 'bg-white'}`}>
      {/* Simplified Background Animation - Reduced particles */}
      <div className="absolute inset-0 overflow-hidden">
        {Array.from({ length: 8 }).map((_, i) => (
          <motion.div
            key={i}
            className="absolute w-1 h-1 bg-gradient-to-r from-blue-400 to-purple-400 rounded-full"
            animate={{
              x: [0, 100, 0],
              y: [0, -50, 0],
              opacity: [0, 0.4, 0],
              scale: [0, 1, 0],
            }}
            transition={{
              duration: 6,
              repeat: Infinity,
              delay: i * 0.5,
              ease: "easeInOut"
            }}
            style={{
              left: `${Math.random() * 100}%`,
              top: `${Math.random() * 100}%`,
            }}
          />
        ))}
      </div>

      {/* Simplified Floating Elements */}
      <motion.div
        animate={{
          y: [0, -10, 0],
          rotate: [0, 2, 0],
        }}
        transition={{
          duration: 4,
          repeat: Infinity,
          ease: "easeInOut"
        }}
        className="absolute top-10 sm:top-20 left-5 sm:left-10 w-12 h-12 sm:w-16 sm:h-16 bg-gradient-to-r from-blue-400 to-purple-400 rounded-full opacity-5 blur-lg"
      />
      
      <motion.div
        animate={{
          y: [0, 10, 0],
          rotate: [0, -2, 0],
        }}
        transition={{
          duration: 3,
          repeat: Infinity,
          ease: "easeInOut",
          delay: 1
        }}
        className="absolute bottom-10 sm:bottom-20 right-5 sm:right-10 w-16 h-16 sm:w-20 sm:h-20 bg-gradient-to-r from-pink-400 to-red-400 rounded-full opacity-5 blur-lg"
      />

      <div className="container mx-auto px-2 sm:px-4 relative z-10">
        {/* Header Section */}
        <motion.div
          ref={ref}
          initial={{ opacity: 0, y: 30 }}
          animate={controls}
          variants={{
            visible: { opacity: 1, y: 0 }
          }}
          transition={{ duration: 0.5 }}
          className="text-center mb-12 sm:mb-16"
        >
          <motion.div
            initial={{ scale: 0 }}
            animate={{ scale: 1 }}
            transition={{ duration: 0.4, delay: 0.1 }}
            className="inline-flex items-center space-x-2 px-4 py-2 sm:px-6 sm:py-3 rounded-full bg-gradient-to-r from-blue-500/20 to-purple-500/20 border border-blue-500/30 mb-4 sm:mb-6"
          >
            <Sparkles size={16} className="text-blue-400 sm:w-5 sm:h-5" />
            <span className={`text-xs sm:text-sm font-medium ${isDark ? 'text-blue-300' : 'text-blue-600'}`}>
              Technical Expertise
            </span>
          </motion.div>
          
          <h2 className={`text-xl sm:text-2xl md:text-3xl font-bold mb-4 sm:mb-6 ${isDark ? 'text-white' : 'text-gray-900'}`}>
            {t('skills.title')}
          </h2>
          <p className={`text-sm sm:text-base max-w-3xl mx-auto ${isDark ? 'text-gray-300' : 'text-gray-600'}`}>
            {t('skills.subtitle')}
          </p>
        </motion.div>

        {/* Skill Categories - Mobile Optimized */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6 md:gap-8 mb-8 sm:mb-12 md:mb-16">
          {skillCategories.map((category, categoryIndex) => (
            <motion.div
              key={category.title}
              initial={{ opacity: 0, y: 20 }}
              animate={controls}
              variants={{
                visible: { opacity: 1, y: 0 }
              }}
              transition={{ duration: 0.4, delay: categoryIndex * 0.1 }}
              whileHover={{ 
                scale: 1.02, 
                rotateY: 2,
                boxShadow: isDark 
                  ? "0 25px 50px -12px rgba(59, 130, 246, 0.25)" 
                  : "0 25px 50px -12px rgba(0, 0, 0, 0.25)"
              }}
              className={`p-4 sm:p-6 rounded-xl ${isDark ? 'bg-slate-800/50' : 'bg-gray-50'} border ${isDark ? 'border-white/10' : 'border-gray-200'} backdrop-blur-sm transition-all duration-300`}
            >
              <div className="flex items-center space-x-2 sm:space-x-3 mb-4 sm:mb-6">
                <motion.div 
                  className={`p-2 sm:p-3 rounded-lg ${isDark ? 'bg-blue-500/20' : 'bg-blue-100'}`}
                  whileHover={{ rotate: 360 }}
                  transition={{ duration: 0.4 }}
                >
                  <category.icon size={20} className="text-blue-500 sm:w-6 sm:h-6" />
                </motion.div>
                <h3 className={`text-lg sm:text-xl font-bold ${isDark ? 'text-white' : 'text-gray-900'}`}>
                  {category.title}
                </h3>
              </div>
              
              <div className="space-y-3 sm:space-y-4">
                {category.skills.map((skill, skillIndex) => (
                  <motion.div
                    key={skill.name}
                    initial={{ opacity: 0, x: -20 }}
                    animate={controls}
                    variants={{
                      visible: { opacity: 1, x: 0 }
                    }}
                    transition={{ duration: 0.4, delay: (categoryIndex * 0.1) + (skillIndex * 0.05) }}
                    onHoverStart={() => setHoveredSkill(skill.name)}
                    onHoverEnd={() => setHoveredSkill(null)}
                    className="group"
                  >
                    <div className="flex justify-between items-center mb-1 sm:mb-2">
                      <span className={`text-xs sm:text-sm font-medium ${isDark ? 'text-gray-300' : 'text-gray-700'}`}>
                        {skill.name}
                      </span>
                      <span className={`text-xs font-bold ${isDark ? 'text-blue-400' : 'text-blue-600'}`}>
                        {skill.level}%
                      </span>
                    </div>
                    <div className={`h-1.5 sm:h-2 rounded-full ${isDark ? 'bg-slate-700' : 'bg-gray-200'} overflow-hidden`}>
                      <motion.div
                        initial={{ width: 0 }}
                        animate={controls}
                        variants={{
                          visible: { width: `${skill.level}%` }
                        }}
                        transition={{ duration: 0.6, delay: (categoryIndex * 0.1) + (skillIndex * 0.05) + 0.2 }}
                        className={`h-full bg-gradient-to-r from-blue-500 to-purple-500 rounded-full transition-all duration-300 ${
                          hoveredSkill === skill.name ? 'shadow-lg shadow-blue-500/50' : ''
                        }`}
                      />
                    </div>
                  </motion.div>
                ))}
              </div>
            </motion.div>
          ))}
        </div>

        {/* Technologies Grid - Mobile Optimized */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={controls}
          variants={{
            visible: { opacity: 1, y: 0 }
          }}
          transition={{ duration: 0.5, delay: 0.3 }}
          className="mb-12 sm:mb-16"
        >
          <div className="text-center mb-8 sm:mb-12">
            <motion.div
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              transition={{ duration: 0.4, delay: 0.4 }}
              className="inline-flex items-center space-x-2 px-4 py-2 sm:px-6 sm:py-3 rounded-full bg-gradient-to-r from-green-500/20 to-blue-500/20 border border-green-500/30 mb-4 sm:mb-6"
            >
              <Zap size={16} className="text-green-400 sm:w-5 sm:h-5" />
              <span className={`text-xs sm:text-sm font-medium ${isDark ? 'text-green-300' : 'text-green-600'}`}>
                {t('skills.technologies')}
              </span>
            </motion.div>
          </div>

          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-3 sm:gap-4 md:gap-6">
            {technologies.map((tech, index) => (
              <motion.div
                key={tech.name}
                initial={{ opacity: 0, scale: 0.8 }}
                animate={controls}
                variants={{
                  visible: { opacity: 1, scale: 1 }
                }}
                transition={{ duration: 0.4, delay: 0.4 + (index * 0.05) }}
                whileHover={{ 
                  scale: 1.05, 
                  y: -5,
                  boxShadow: isDark 
                    ? "0 20px 40px -12px rgba(59, 130, 246, 0.3)" 
                    : "0 20px 40px -12px rgba(0, 0, 0, 0.2)"
                }}
                className={`p-3 sm:p-4 rounded-xl text-center ${isDark ? 'bg-slate-800/50' : 'bg-gray-50'} border ${isDark ? 'border-white/10' : 'border-gray-200'} backdrop-blur-sm transition-all duration-300 cursor-pointer`}
              >
                <div className="text-2xl sm:text-3xl mb-2 sm:mb-3">{tech.icon}</div>
                <h4 className={`text-xs sm:text-sm font-semibold mb-1 sm:mb-2 ${isDark ? 'text-white' : 'text-gray-900'}`}>
                  {tech.name}
                </h4>
                <span className={`text-xs px-2 py-1 rounded-full ${isDark ? 'bg-blue-500/20 text-blue-300' : 'bg-blue-100 text-blue-600'}`}>
                  {tech.level}
                </span>
              </motion.div>
            ))}
          </div>
        </motion.div>

        {/* Currently Learning Section - Mobile Optimized */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          animate={controls}
          variants={{
            visible: { opacity: 1, y: 0 }
          }}
          transition={{ duration: 0.5, delay: 0.6 }}
        >
          <div className="text-center mb-8 sm:mb-12">
            <motion.div
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              transition={{ duration: 0.4, delay: 0.7 }}
              className="inline-flex items-center space-x-2 px-4 py-2 sm:px-6 sm:py-3 rounded-full bg-gradient-to-r from-purple-500/20 to-pink-500/20 border border-purple-500/30 mb-4 sm:mb-6"
            >
              <TrendingUp size={16} className="text-purple-400 sm:w-5 sm:h-5" />
              <span className={`text-xs sm:text-sm font-medium ${isDark ? 'text-purple-300' : 'text-purple-600'}`}>
                {t('skills.learning')}
              </span>
            </motion.div>
            <p className={`text-sm sm:text-base max-w-2xl mx-auto ${isDark ? 'text-gray-300' : 'text-gray-600'}`}>
              {t('skills.learningDesc')}
            </p>
          </div>

          <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-5 gap-3 sm:gap-4 md:gap-6">
            {learningItems.map((item, index) => (
              <motion.div
                key={item.name}
                initial={{ opacity: 0, y: 20 }}
                animate={controls}
                variants={{
                  visible: { opacity: 1, y: 0 }
                }}
                transition={{ duration: 0.4, delay: 0.7 + (index * 0.05) }}
                whileHover={{ 
                  scale: 1.05, 
                  y: -3,
                  boxShadow: isDark 
                    ? "0 15px 30px -12px rgba(147, 51, 234, 0.3)" 
                    : "0 15px 30px -12px rgba(0, 0, 0, 0.15)"
                }}
                className={`p-3 sm:p-4 rounded-xl text-center ${isDark ? 'bg-slate-800/30' : 'bg-gray-50/50'} border ${isDark ? 'border-white/5' : 'border-gray-200'} backdrop-blur-sm transition-all duration-300`}
              >
                <div className="text-xl sm:text-2xl mb-1 sm:mb-2">{item.icon}</div>
                <h4 className={`text-xs font-medium ${isDark ? 'text-gray-300' : 'text-gray-700'}`}>
                  {item.name}
                </h4>
              </motion.div>
            ))}
          </div>
        </motion.div>
      </div>
    </section>
  );
};

export default Skills;